

const Person = (props) => {

    function handleClick() {
        alert("naber la");
    }

    return (

        <div onClick={handleClick} id="person_div" className="d-flex border m-1">
            <div className="w-25">
                <img src={props.img} alt="profil foto" className="w-75" />
            </div>

            <h4>{props.name}</h4>
        </div>
    )


}





export default Person;